import React, { Component } from "react";
import Total from "./components/total/Total";
import History from "./components/history/History";
import Operation from "./components/operation/Operation";

class App extends Component {
  state = {
    transactions: JSON.parse(localStorage.getItem(`calcMoney1`)) || [],
    description: "",
    amount: "",
    resultIncome: 0,
    resultExpenses: 0,
    totalBalance: 0,
  };

  componentWillMount() {
    this.getTotalBalance();
  }

  componentDidUpdate() {
    this.addStorage();
  }

  /* Функция добавления операции*/
  addTransaction = (add) => {
    const transactions = [
      ...this.state.transactions,
      {
        id: `cmr${(+new Date()).toString(16)}`,
        description: this.state.description,
        amount: parseFloat(this.state.amount),
        add,
      },
    ];

    this.setState(
      {
        transactions,
        description: "",
        amount: "",
      },
      this.getTotalBalance
    );
  };

  /* Название операции */
  addDescription = (e) => {
    this.setState({ description: e.target.value });
  };
  /* Сумма операции */
  addAmount = (e) => {
    this.setState({ amount: e.target.value });
  };

  /* Доходы */
  getIncome = () =>
    this.state.transactions.reduce(
      (acc, item) => (item.add ? item.amount + acc : acc),
      0
    );
  /* Расходы */
  getExpenses = () =>
    this.state.transactions.reduce(
      (acc, item) => (!item.add ? item.amount + acc : acc),
      0
    );
  /* Баланс */
  getTotalBalance() {
    const resultIncome = this.getIncome();
    const resultExpenses = this.getExpenses();

    const totalBalance = resultIncome - resultExpenses;

    this.setState({
      resultIncome,
      resultExpenses,
      totalBalance,
    });
  }
  /* Сохранение в хранилище */
  addStorage() {
    localStorage.setItem("calcMoney1", JSON.stringify(this.state.transactions));
  }
  /* Очистка операции */
  delTransaction = (key) => {
    const transactions = this.state.transactions.filter(
      (item) => item.id !== key
    );
    this.setState({ transactions }, this.getTotalBalance);
  };

  render() {
    return (
      <>
        <header>
          <h1>KopilOchka</h1>
          <h2>Калькулятор бюджета</h2>
        </header>

        <main>
          <div className="container">
            <Total
              totalBalance={this.state.totalBalance}
              resultIncome={this.state.resultIncome}
              resultExpenses={this.state.resultExpenses}
            />

            <History
              transactions={this.state.transactions}
              delTransaction={this.delTransaction}
            />

            <Operation
              addTransaction={this.addTransaction}
              addAmount={this.addAmount}
              addDescription={this.addDescription}
              description={this.state.description}
              amount={this.state.amount}
            />
          </div>
        </main>
      </>
    );
  }
}

export default App;
